import { Component, OnInit } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
declare var jQuery: any;
declare var THREE: any;
declare var destination: any;
/* declare  var particlesJS:  any; */

@Component({
  selector: 'app-business-index',
  templateUrl: './business-index.component.html',
  styleUrls: ['./business-index.component.css'],
  encapsulation: ViewEncapsulation.None,
})
export class BusinessIndexComponent implements OnInit {
  profile: any = {
    logo: 'assets/logo.png',
    profile: '',
  };

  member: any = {
    image: '',
    logo: 'assets/images/myphoto.jpg',
    myPhotoWithoutBackground: 'assets/images/background/myphoto-removebg.png',
    designation: this.translate.instant('Full Stack Developer'),
    age: '29',
    dob: '',
    residence: 'CANADA',
    address: '22 Rue Saint-Jules',
    email: 'bbsanakinao@gmail.com',
    phone: '+1 (418) 261 6827',
    skype: '',
    whatsapp: '+1 (418) 261 6827',
    about_me: this.translate.instant('about_me'),
    intrests: [
      this.translate.instant('Secourisme'),
      this.translate.instant('Bénévolat'),
      this.translate.instant('Cinéma'),
      this.translate.instant('Chant'),
      this.translate.instant('Voyages'),
      this.translate.instant('Cuisine'),
      this.translate.instant('Photographie'),
      this.translate.instant('Musique'),
      this.translate.instant('Jeux vidéos'),
      this.translate.instant('Mangas'),
    ],
    name: 'K. Betchaleel BANAKINAO',
    study: this.translate.instant('Student MsC. in Artificial Intelligence'),
    highes_degree: this.translate.instant(
      'Computer Engineer: Software Engineering and Information Systems'
    ),
    profile: this.translate.instant(
      'Full Stack Developer and Student MsC. in Artificial Intelligence'
    ),
    page: 'business-man',
  };

  constructor(public translate: TranslateService) {
    this.update_translation();
  }

  ngOnInit(): void {
    (function ($) {
      setTimeout(function () {
        var SEPARATION = 100,
          AMOUNTX = 100,
          AMOUNTY = 50;

        var container;
        var destination: any;
        var camera, scene, renderer;

        var particles,
          particle,
          count = 0;

        var mouseX = 85,
          mouseY = -342;

        var windowHalfX = window.innerWidth / 2;
        var windowHalfY = window.innerHeight / 2;

        init();
        animate();

        function init() {
          if (jQuery('canvas').length > 0) {
            jQuery('canvas').remove();
          }

          //container = document.createElement('');
          //container = document.createElement('.wave-effect');
          destination = document.getElementById('WaveEffect');
          //document.body.appendChild(container);
          //destination.appendChild(container);

          camera = new THREE.PerspectiveCamera(
            120,
            window.innerWidth / window.innerHeight,
            1,
            10000
          );
          camera.position.z = 1000;

          scene = new THREE.Scene();

          particles = new Array();

          var PI2 = Math.PI * 2;
          var material = new THREE.ParticleCanvasMaterial({
            color: 0x008aff,
            program: function (context) {
              context.beginPath();
              context.arc(0, 0, 0.6, 0, PI2, true);
              context.fill();
            },
          });

          var i = 0;

          for (var ix = 0; ix < AMOUNTX; ix++) {
            for (var iy = 0; iy < AMOUNTY; iy++) {
              particle = particles[i++] = new THREE.Particle(material);
              particle.position.x =
                ix * SEPARATION - (AMOUNTX * SEPARATION) / 2;
              particle.position.z =
                iy * SEPARATION - (AMOUNTY * SEPARATION) / 2;
              scene.add(particle);
            }
          }

          renderer = new THREE.CanvasRenderer();
          renderer.setSize(window.innerWidth, window.innerHeight);
          //container.appendChild(renderer.domElement);
          //alert(renderer.domElement.html());
          destination.appendChild(renderer.domElement);

          document.addEventListener('mousemove', onDocumentMouseMove, false);
          document.addEventListener('touchstart', onDocumentTouchStart, false);
          document.addEventListener('touchmove', onDocumentTouchMove, false);

          //

          window.addEventListener('resize', onWindowResize, false);

          //window.addEventListener('orientationchange', onWindowResize, false);
        }

        function onWindowResize() {
          windowHalfX = window.innerWidth / 2;
          windowHalfY = window.innerHeight / 2;

          camera.aspect = window.innerWidth / window.innerHeight;
          camera.updateProjectionMatrix();

          renderer.setSize(window.innerWidth, window.innerHeight);
        }

        //

        function onDocumentMouseMove(event) {
          mouseX = event.clientX - windowHalfX;
          mouseY = event.clientY - windowHalfY;
        }

        function onDocumentTouchStart(event) {
          if (event.touches.length === 1) {
            event.preventDefault();

            mouseX = event.touches[0].pageX - windowHalfX;
            mouseY = event.touches[0].pageY - windowHalfY;
          }
        }

        function onDocumentTouchMove(event) {
          if (event.touches.length === 1) {
            event.preventDefault();

            mouseX = event.touches[0].pageX - windowHalfX;
            mouseY = event.touches[0].pageY - windowHalfY;
          }
        }

        //

        function animate() {
          requestAnimationFrame(animate);

          render();
        }

        function render() {
          camera.position.x += (mouseX - camera.position.x) * 0.05;
          camera.position.y += (-mouseY - camera.position.y) * 0.05;
          camera.lookAt(scene.position);

          var i = 0;

          for (var ix = 0; ix < AMOUNTX; ix++) {
            for (var iy = 0; iy < AMOUNTY; iy++) {
              particle = particles[i++];
              particle.position.y =
                Math.sin((ix + count) * 0.3) * 50 +
                Math.sin((iy + count) * 0.5) * 50;
              particle.scale.x = particle.scale.y =
                (Math.sin((ix + count) * 0.3) + 1) * 2 +
                (Math.sin((iy + count) * 0.5) + 1) * 2;
            }
          }

          renderer.render(scene, camera);
          count += 0.1;
        }
        jQuery(window).on('resize', function (e) {
          var windowHalfX = window.innerWidth / 2;
          var windowHalfY = window.innerHeight / 2;
          init();
          animate();
        });
      }, 500);
    })(jQuery);
  }

  update_translation() {
    this.member = JSON.parse(sessionStorage.getItem('member'));

    this.translate.onLangChange.subscribe((event) => {
      this.member = {
        image: '',
        logo: 'assets/images/myphoto.jpg',
        myPhotoWithoutBackground:
          'assets/images/background/myphoto-removebg.png',
        designation: this.translate.instant('Full Stack Developer'),
        age: '29',
        dob: '',
        residence: 'CANADA',
        address: "Québec,Québec",
        email: 'bbsanakinao@gmail.com',
        phone: '+1 (418) 261 6827',
        skype: '',
        whatsapp: '+1 (418) 261 6827',
        about_me: this.translate.instant('about_me'),
        intrests: [
          this.translate.instant('First Aid'),
          this.translate.instant('Volunteering'),
          this.translate.instant('Cinema'),
          this.translate.instant('Singing'),
          this.translate.instant('Travel'),
          this.translate.instant('Cooking'),
          this.translate.instant('Photography'),
          this.translate.instant('Music'),
          this.translate.instant('Video Games'),
          this.translate.instant('Manga'),
        ],
        name: 'K. Betchaleel BANAKINAO',
        study: this.translate.instant(
          'Student MsC. in Artificial Intelligence'
        ),
        highes_degree: this.translate.instant(
          'Computer Engineer: Software Engineering and Information Systems'
        ),
        profile: this.translate.instant(
          'Full Stack Developer and Student MsC. in Artificial Intelligence'
        ),
        page: 'business-man',
      };

      sessionStorage.setItem('member', JSON.stringify(this.member));
    });
  }
}
