import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { environment } from 'src/environments/environment';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import Swal from 'sweetalert2';
import { TranslateService } from '@ngx-translate/core';
declare  var jQuery:  any;

@Component({
  selector: 'app-business-contact-us',
  templateUrl: './business-contact-us.component.html',
  styleUrls: ['./business-contact-us.component.css']
})
export class BusinessContactUsComponent implements OnInit {

  contactForm: FormGroup;

  profile : any = {
                  logo:"assets/logo.png",
                  profile:"business-man"
            };

  page_banner : any = {
        title:"Contact Us",
        profile:"business-man",
  };

  data={
    name:'',
    email:'',
    phone:'',
    message:'',
    subject:''
  }

  constructor(private httpClient:HttpClient,private fb: FormBuilder,private translate:TranslateService) { }

  sendMessage(){{
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json',
        // Authorization: 'Basic ' + btoa(`${environment.apiKeyPublic}:${environment.apiKeyPrivate}`),
      }),
    };
   let body={
      Messages:[
          {
              From: {
                  Email: 'mmarsasso@gmail.com' ,
                  Name: this.data.name ,
              },
              To: [
                  {
                      Email: "bbsanakinao@gmail.com",
                      Name: "BANAKINAO K. Betchaleel"
                  }
              ],
              Subject: this.data.subject+" Portofolio - CONTACT",
              TextPart: this.data.message,
             HTMLPart: "<h3>Sender phone: "+this.data.phone+"</h3> <br> <h3> Email: "+this.data.email+"</h3>",
          }
      ]
    }
    this.httpClient.post('https://mailjet-api-betch130s-projects.vercel.app/send-email', body,httpOptions).subscribe(x=>{
      console.log(x);
      this.data={
        name:'',
        email:'',
        phone:'',
        message:'',
        subject:''
      }
      this.contactForm.reset(this.data)
      Swal.fire({
        icon: 'success',
        title: this.translate.instant('Merci!'),
        text: this.translate.instant('Votre message a été envoyé avec succès. Nous vous répondrons bientôt.'),
        confirmButtonText: 'OK'
      });
    },error=>{
      console.log(error);

      Swal.fire({
        icon: 'error',
        title: this.translate.instant('Erreur'),
        text: this.translate.instant('Il y a eu un problème lors de l\'envoi de votre message. Veuillez réessayer plus tard.'),
        confirmButtonText: 'OK'
      });
    });
  }
}

onSubmit() {
  if (this.contactForm.valid) {
    console.log(this.contactForm.value);
    this.data=this.contactForm.value;
    this.sendMessage()
  } else {
    console.log('Formulaire non valide');
  }
}

    ngOnInit(): void {
      this.contactForm = this.fb.group({
        name: ['', Validators.required],
        email: ['', [Validators.required, Validators.email]],
        phone: ['', Validators.required],
        subject: ['', Validators.required],
        message: ['', Validators.required],
        recaptcha: [null]
      });
      (function ($) {
        setTimeout(function(){
            if(jQuery('.dezPlaceAni').length) {

              $('.dezPlaceAni input, .dezPlaceAni textarea').on('focus',function(){
                $(this).parents('.form-group, .news-box').addClass('focused');
              });

              $('.dezPlaceAni input, .dezPlaceAni textarea').on('blur',function(){
                var inputValue = $(this).val();
                if ( inputValue == "" ) {
                $(this).removeClass('filled');
                $(this).parents('.form-group, .news-box').removeClass('focused');
                } else {
                $(this).addClass('filled');
                }
              })
            }
        }, 500);
      })(jQuery);

    }

}
