import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';

@Component({
  selector: 'app-portofolio-details',
  templateUrl: './portofolio-details.component.html',
  styleUrls: ['./portofolio-details.component.css']
})
export class PortofolioDetailsComponent implements OnInit {

profile : any = {
    logo:"assets/logo.png",
    profile:"business-man"
};

page_banner : any = {
    title:"Portfolio/",
    profile:"business-man",
};
portofolio:any;
  constructor(private translate:TranslateService,private sanitizer: DomSanitizer) { this.update()}
  safeUrl: SafeResourceUrl;
  ngOnInit(): void {
    this.portofolio=JSON.parse(sessionStorage.getItem('portfolio'));
    this.safeUrl=this.sanitizer.bypassSecurityTrustResourceUrl(this.portofolio.link);
    this.page_banner.title+=this.portofolio.title;
    console.log(this.safeUrl);

  }

  update(){
    this.translate.onLangChange.subscribe((event) => {
      this.portofolio=JSON.parse(sessionStorage.getItem('portfolio'));
      this.safeUrl=this.sanitizer.bypassSecurityTrustResourceUrl(this.portofolio.link);
      console.log(this.portofolio);
      console.log(this.safeUrl);

    })
  }



}
